@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:ital,wght@0,400;0,700;1,400;1,700&family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
.spinner-container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;padding:50px;-webkit-align-items:center;align-items:center}.spinner-container .spinner{padding-right:10px}body{background-color:#f2f3f7 !important;font-size:16px}.container{display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;-webkit-align-content:center;align-content:center;width:90%;height:100px;margin:0em auto}h1{color:#021d3a;line-height:1.25;margin-bottom:0.5em}h2{color:#021d3a;font-size:24px;line-height:1.25}a,a:active,a:link{color:#021d3a;text-decoration:none}.page{width:90%;max-width:960px;margin:0em auto;margin-top:30px;transition:all 500ms ease}.logo{height:40px}.tab-slider--tab{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;float:left;margin:0;padding:0px;list-style:none;position:relative;border-radius:35px;background:#e9e9ed;height:40px}.tab-slider--tab:after{content:"";width:15%;background:white;display:-webkit-flex;display:flex;height:80%;position:absolute;top:4px;left:5px;transition:all 250ms ease-in-out;border-radius:35px}.tab-slider--tab.slideAll:after{width:19%;left:4px}.tab-slider--tab.slideAboutMe:after{left:21%;width:42%}.tab-slider--tab.slideProjects:after{left:62%;width:36%}.tab-slider--text{font-size:15px;line-height:1;text-align:center;padding:11px 18px;position:relative;z-index:2;cursor:pointer;display:inline-block;transition:color 250ms ease-in-out}.tab-slider--text:hover{opacity:0.7}.tab-slider--text:active:visited:focus{background-color:transparent}.tab-slider--content{display:none}.tab-slider--content.show{display:block}*::before,*::after,*{box-sizing:border-box;margin:0;padding:0}:root{font-family:"Open Sans",sans-serif;font-weight:400;color:#021d3a}.react-grid-layout{position:relative;transition:height 1000ms ease}.react-grid-item{transition:all 2000ms ease;transition-property:left, top}.react-grid-item.cssTransforms{transition-property:-webkit-transform;transition-property:transform;transition-property:transform, -webkit-transform}.react-grid-item.resizing{z-index:1;will-change:width, height}.react-grid-item.react-draggable-dragging{transition:none;z-index:3;will-change:transform}.react-grid-item.react-grid-placeholder{background:rgba(32,32,32,0.534);border-radius:30px;opacity:0.2;transition-duration:1000ms;z-index:2;-webkit-user-select:none;-ms-user-select:none;-o-user-select:none;user-select:none}

*::before,
*::after,
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

